.resume-container{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-width: fit-content;
    margin: 120px 0;
}

.resume-card{
    display: flex;
    height: 500px;
    width: 1000px;

}
.resume-bullets{
    height: 100%;
    box-shadow: 15px 0 9px -15px #1f2235;
    width: 320px;
}
.resume-bullet-details{
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
}
.experience-description{
    margin: 0;
    text-align: justify;
    max-height: 60%;
    max-width: 90%;
    font-size: small;
}
.resume-details-carousal{
    transition: transform 1s ease-out;
}
.bullet-container{
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
}
.bullets{
    width: 80%;
    position: relative;
    z-index: 2;
}
.bullet{
    display: flex;
    align-items: center;
    background-color: #1f2235;
    height: 40px;
    margin: 30px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
    cursor: pointer;
}
.bullet-label{
    font-size: 14px;
    white-space: nowrap;
    font-family: "Montserrat Medium";
}
.bullet-logo{
    height: 16px;
    margin: 0 30px 0 0;
}
.bullet-icons{
    width: 34px;
    height: 100%;
    z-index: 1;
    background-color: #1f2235;
    position: absolute;
}
.selected-bullet {
    color: white;
    width: 100%;
}
.resume-screen-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 500px;
    width: 100%;
}
.experience-container{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    overflow-y: scroll;
}
.resume-heading{
    display: flex;
    flex-direction: column;
}
.resume-main-heading{
    margin-top:50px;
    position: relative;
    display: flex;
    justify-content: space-between;
}
.heading-bullet{
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    background-color: var(--light-yellow);
    border-radius: 25%;
}
.resume-sub-heading{
    font-size: 14px;
    font-family: "Montserrat Medium";
    color: #111;
    margin: 10px 0 0 0;
}
.resume-heading-description{
    font-size: 12px;
    text-align: justify;
}
.heading-date{
    background-color: var(--light-yellow);
    padding: 4px 12px;
    font-size: 14px;
    border-radius: 14px;
    color: black;
}
.skill-parent{
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
}
.programming-skills-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.application-skills-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.skill-percentage{
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--light-yellow);
}
.skill-percentage .active-percentage-bar{
    height: 15px;
    transition: 0.8s ease;
    position: absolute;
    background-color: #1f2235;
}
.skill-parent span{
    font-family: "OstrichSans Black";
    font-size: 16px;
    color: var(--dark-orange);
}

/* IPAD PRO RESPONSIVENESS */
@media only screen and (max-width: 1110px) {
    .resume-card {
      width: 880px;
    }
  
    .resume-bullet-details {
      padding: 0 0 0 70px;
    }
  }
  
  /* IPAD RESPONSIVENESS */
  @media only screen and (max-width: 1023px) {
    .resume-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .bullets {
      width: 90%;
    }
  
    .resume-card {
      height: fit-content;
      display: flex;
      flex-direction: column;
      margin: 30px 0;
      align-items: center;
      width: 90%;
    }
  
    .resume-bullet-details {
      height: 500px;
      padding: 0;
      width: fit-content;
    }
  
    .resume-bullets {
      margin: 30px 0;
      width: 100%;
    }
  
    .selected-bullet {
      width: 100%;
    }
  }

.experience-container::-webkit-scrollbar {
    width: 20px; 
 }


.experience-container::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2) !important;
  border-radius: 25px !important;
}